
body {
  margin: 0;
  padding: 0;
  background-color: #e3f0fd;
  height: 100%;
}


html,
body,
#potato,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  font-weight:600;
  padding-top:300%;
}


.movies .movie {
  display: flex;
  padding: 20px;
  padding-top: 80px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  position: relative;
  top: -50px;
  width: 120px;
  height: 200px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title {
  margin-top:-40px;
  margin-bottom: 5px;
  font-size: 24px;
  font-weight: 500;  
  color: #2c2c2c;
}

.movie .movie__summary {
  font-size: 10px;
  color: #444444;
  padding-right: 80px;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  font-style: italic;
  margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
  margin-right: 10px;
  font-size: 14px;
}